	module.exports = {"data":{"markdownRemark":{"html":"<h3>What is HackerOne Clearance?</h3>\n<p>HackerOne Clearance is a pool of hackers that have satisfied strict background and identity checks, and are also active and high-performing members of the HackerOne community.   </p>\n<h3>What are the benefits of having HackerOne Clearance?</h3>\n<p>Having HackerOne Clearance makes you eligible to participate in:</p>\n<ul>\n<li>HackerOne Clear programs (programs that require you to have Clearance in order to participate in)</li>\n<li>Live hacking events that require clearance</li>\n<li><a href=\"/hackers/retesting.html\">Retesting</a></li>\n</ul>\n<h3>How does a hacker get HackerOne Clearance?</h3>\n<p>To obtain HackerOne Clearance, you need to be a member of the HackerOne community that produces high quality findings, acts responsibly and behaves professionally (zero <a href=\"https://hackerone.com/disclosure-guidelines\">Code of Conduct</a> violations). You’ll specifically be invited to obtain HackerOne Clearance through which you’ll need to agree to a stricter set of terms called the H1 Clear Finder Additional Terms. (These terms build on our regular hacker terms but have stricter confidentiality terms and higher expectations around professional conduct.)</p>\n<p>After agreeing to the terms, you’ll need to pass a criminal background check. When the background check comes back without any red flags, you’ll be awarded your Clearance.</p>\n<blockquote>\n<p><b>Note:</b> The background check is repeated on an annual basis and is required to retain your Clearance.</p>\n</blockquote>","frontmatter":{"title":"HackerOne Clearance","id":"hackers/hackerone-clearance","path":"/hackers/hackerone-clearance.html","description":null}}},"pathContext":{}}

